.empty-state svg { color: var(--gray-light); margin-bottom: 1rem; }
.customer-card { background: var(--white); border-radius: 12px; padding: 1.25rem; box-shadow: 0 2px 8px rgba(0,0,0,0.05); border-left: 4px solid var(--gray-light); }
