.search-filter-bar { display: flex; gap: 1rem; margin-bottom: 1.5rem; flex-wrap: wrap; }
.search-box { display: flex; align-items: center; gap: 0.75rem; flex: 1; min-width: 250px; padding: 0.75rem 1rem; background: var(--white); border-radius: 10px; box-shadow: 0 2px 8px rgba(0,0,0,0.05); }
