.milestone-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 0.5rem; }
.milestone-header h4 { font-size: 1rem; }
.milestone-category { padding: 0.2rem 0.5rem; background: var(--light); border-radius: 4px; font-size: 0.75rem; color: var(--gray); }
.milestone-content p { color: var(--gray); font-size: 0.9rem; margin-bottom: 0.5rem; }
.milestone-date { display: flex; align-items: center; gap: 0.25rem; font-size: 0.8rem; color: var(--primary); }
.milestone-actions { display: flex; gap: 0.25rem; }
.swot-form-grid { display: grid; grid-template-columns: repeat(2, 1fr); gap: 1rem; margin: 1rem 0; }
.swot-form-section { padding: 1rem; border-radius: 10px; }
.swot-form-section h4 { margin-bottom: 0.75rem; font-size: 0.95rem; }
.swot-input-row { display: flex; gap: 0.5rem; margin-bottom: 0.5rem; }
.swot-input-row input { flex: 1; padding: 0.5rem; border: 1px solid var(--gray-light); border-radius: 6px; }
.remove-btn { background: none; border: none; color: var(--gray-light); cursor: pointer; }
.remove-btn:hover { color: var(--danger); }
.key-results-section { margin: 1rem 0; }
.key-results-section h4 { margin-bottom: 0.75rem; }
.kr-form-row { display: flex; gap: 0.5rem; margin-bottom: 0.5rem; }
.kr-text-input { flex: 2; }
.kr-num-input { width: 80px; }
.kr-unit-input { width: 80px; }

/* Marketing Page */
.marketing-page { max-width: 1100px; margin: 0 auto; }
.calendar-section { background: var(--white); border-radius: 16px; padding: 1.5rem; box-shadow: 0 2px 8px rgba(0,0,0,0.05); }
.calendar-header { display: flex; justify-content: center; align-items: center; gap: 2rem; margin-bottom: 1.5rem; }
.calendar-header h2 { font-size: 1.25rem; min-width: 200px; text-align: center; }
.calendar-header .nav-btn { background: var(--light); border: none; width: 36px; height: 36px; border-radius: 8px; cursor: pointer; font-size: 1.1rem; transition: all 0.2s; }
.calendar-header .nav-btn:hover { background: var(--primary); color: var(--white); }
.calendar-day-names { display: grid; grid-template-columns: repeat(7, 1fr); gap: 4px; margin-bottom: 0.5rem; }
.day-name { text-align: center; font-weight: 600; font-size: 0.85rem; color: var(--gray); padding: 0.5rem; }
.calendar-days { display: grid; grid-template-columns: repeat(7, 1fr); gap: 4px; }
.calendar-day { min-height: 80px; padding: 0.5rem; background: var(--light); border-radius: 8px; cursor: pointer; transition: all 0.2s; }
.calendar-day.empty { background: transparent; cursor: default; }
.calendar-day.active:hover { background: rgba(99, 102, 241, 0.1); }
.calendar-day.today { border: 2px solid var(--primary); }
.day-number { font-weight: 500; font-size: 0.9rem; }
.day-events { display: flex; gap: 4px; margin-top: 0.25rem; flex-wrap: wrap; }
.event-dot { width: 8px; height: 8px; border-radius: 50%; }
.more-events { font-size: 0.7rem; color: var(--gray); }
.calendar-legend { display: flex; justify-content: center; gap: 1.5rem; margin-top: 1rem; padding-top: 1rem; border-top: 1px solid var(--light); flex-wrap: wrap; }
.legend-dot { width: 10px; height: 10px; border-radius: 50%; }
.upcoming-events { margin-top: 1.5rem; padding-top: 1.5rem; border-top: 1px solid var(--light); }
.upcoming-events h3 { margin-bottom: 1rem; }
.upcoming-event-item { display: flex; align-items: center; gap: 1rem; padding: 0.75rem; background: var(--light); border-radius: 8px; margin-bottom: 0.5rem; }
.event-color { width: 4px; height: 100%; min-height: 40px; border-radius: 2px; }
.event-info { flex: 1; }
.event-title { font-weight: 500; display: block; }
.event-date { font-size: 0.8rem; color: var(--gray); }
.event-type { font-size: 0.8rem; padding: 0.2rem 0.5rem; background: var(--white); border-radius: 4px; text-transform: capitalize; }
.event-actions { display: flex; gap: 0.25rem; }
.no-events { color: var(--gray); font-size: 0.9rem; }
.social-section .section-header { margin-bottom: 1.5rem; }
.social-platforms { display: grid; grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); gap: 1rem; margin-bottom: 2rem; }
.platform-card { background: var(--white); border-radius: 12px; padding: 1.25rem; box-shadow: 0 2px 8px rgba(0,0,0,0.05); text-align: center; }
.platform-header { display: flex; flex-direction: column; align-items: center; gap: 0.5rem; }
.platform-header svg { color: var(--primary); }
.platform-header span { font-weight: 500; }
.post-count { font-size: 0.8rem; color: var(--gray); }
.scheduled-posts h3 { margin-bottom: 1rem; }
.posts-list { display: flex; flex-direction: column; gap: 0.75rem; }
.post-item { display: flex; align-items: center; gap: 1rem; padding: 1rem; background: var(--white); border-radius: 12px; box-shadow: 0 2px 8px rgba(0,0,0,0.05); }
.post-platform-icon { color: var(--primary); flex-shrink: 0; }
.post-content { flex: 1; }
.post-content p { font-size: 0.9rem; margin-bottom: 0.25rem; }
.post-date { font-size: 0.8rem; color: var(--gray); }
.post-status { font-size: 0.8rem; padding: 0.2rem 0.5rem; border-radius: 4px; text-transform: capitalize; }
.post-status.draft { background: var(--light); color: var(--gray); }
.post-status.scheduled { background: rgba(99, 102, 241, 0.1); color: var(--primary); }
.post-status.published { background: rgba(16, 185, 129, 0.1); color: var(--success); }
.post-actions { display: flex; gap: 0.25rem; }
.email-section .templates-grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); gap: 1rem; }
.email-section .template-card { display: flex; flex-direction: column; align-items: stretch; text-align: left; padding: 1.25rem; border: none; background: var(--white); box-shadow: 0 2px 8px rgba(0,0,0,0.05); cursor: default; }
.email-section .template-header { display: flex; align-items: center; gap: 0.75rem; margin-bottom: 0.75rem; }
.email-section .template-header svg { color: var(--primary); }
.email-section .template-header h4 { flex: 1; }
.email-section .template-category { padding: 0.2rem 0.5rem; background: var(--light); border-radius: 4px; font-size: 0.75rem; color: var(--gray); }
.template-preview { font-size: 0.85rem; color: var(--gray); flex: 1; }
.template-preview strong { color: var(--dark); }
.template-preview p { margin-top: 0.25rem; }
.template-actions { display: flex; gap: 0.5rem; margin-top: 1rem; }
.campaigns-list { display: flex; flex-direction: column; gap: 1rem; }
.campaign-card { background: var(--white); border-radius: 12px; padding: 1.5rem; box-shadow: 0 2px 8px rgba(0,0,0,0.05); }
.campaign-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 0.5rem; }
.campaign-header h3 { font-size: 1.1rem; }
.campaign-status { padding: 0.25rem 0.75rem; border-radius: 20px; font-size: 0.8rem; font-weight: 500; text-transform: capitalize; }
.campaign-status.draft { background: var(--light); color: var(--gray); }
.campaign-status.active { background: rgba(16, 185, 129, 0.1); color: var(--success); }
.campaign-status.completed { background: rgba(99, 102, 241, 0.1); color: var(--primary); }
.campaign-status.paused { background: rgba(245, 158, 11, 0.1); color: var(--warning); }
.campaign-dates { display: flex; align-items: center; gap: 0.5rem; font-size: 0.85rem; color: var(--gray); margin-bottom: 1rem; }
.campaign-budget { margin-bottom: 1rem; }
.budget-info { display: flex; justify-content: space-between; font-size: 0.9rem; margin-bottom: 0.5rem; }
.campaign-metrics { display: grid; grid-template-columns: repeat(auto-fit, minmax(120px, 1fr)); gap: 1rem; }
.metric { display: flex; flex-direction: column; align-items: center; padding: 1rem; background: var(--light); border-radius: 10px; text-align: center; }
.metric svg { color: var(--primary); margin-bottom: 0.5rem; }
.metric-value { font-size: 1.25rem; font-weight: 600; }
.metric-label { font-size: 0.75rem; color: var(--gray); margin-bottom: 0.5rem; }
.metric-input { width: 80px; padding: 0.25rem; border: 1px solid var(--gray-light); border-radius: 4px; text-align: center; font-size: 0.85rem; }
.campaign-actions { display: flex; justify-content: flex-end; gap: 0.25rem; margin-top: 1rem; }
.char-count { font-size: 0.8rem; color: var(--gray); text-align: right; display: block; margin-top: 0.25rem; }

/* Settings Page */
.settings-page { max-width: 1000px; margin: 0 auto; }
.save-message { display: flex; align-items: center; gap: 0.5rem; padding: 1rem; background: rgba(16, 185, 129, 0.1); color: var(--success); border-radius: 10px; margin-bottom: 1rem; }
.settings-container { display: grid; grid-template-columns: 220px 1fr; gap: 2rem; }
.settings-sidebar { display: flex; flex-direction: column; gap: 0.5rem; }
.sidebar-item { display: flex; align-items: center; gap: 0.75rem; padding: 0.875rem 1rem; background: none; border: none; border-radius: 10px; font-size: 0.95rem; color: var(--gray); cursor: pointer; transition: all 0.2s; text-align: left; }
.sidebar-item:hover { background: var(--light); color: var(--dark); }
.sidebar-item.active { background: var(--gradient); color: var(--white); }
.settings-content { background: var(--white); border-radius: 16px; padding: 2rem; box-shadow: 0 2px 8px rgba(0,0,0,0.05); }
.settings-section h2 { display: flex; align-items: center; gap: 0.5rem; font-size: 1.25rem; margin-bottom: 0.5rem; }
.settings-section h2 svg { color: var(--primary); }
.section-description { color: var(--gray); margin-bottom: 1.5rem; }
.profile-form { display: flex; flex-direction: column; gap: 1rem; }
.appearance-options, .notification-options { display: flex; flex-direction: column; gap: 1rem; }
.option-card { display: flex; align-items: center; gap: 1rem; padding: 1rem; background: var(--light); border-radius: 12px; }
.option-info { flex: 1; }
.option-info h4 { margin-bottom: 0.25rem; }
.option-info p { font-size: 0.85rem; color: var(--gray); }
.toggle-btn { width: 50px; height: 28px; background: var(--gray-light); border: none; border-radius: 14px; cursor: pointer; position: relative; transition: background 0.2s; }
.toggle-btn.active { background: var(--primary); }
.toggle-slider { position: absolute; top: 3px; left: 3px; width: 22px; height: 22px; background: var(--white); border-radius: 50%; transition: transform 0.2s; }
.toggle-btn.active .toggle-slider { transform: translateX(22px); }
.theme-preview { margin-top: 1.5rem; }
.theme-preview h4 { margin-bottom: 0.75rem; }
.preview-box { width: 100%; max-width: 300px; aspect-ratio: 16/9; background: var(--light); border-radius: 8px; overflow: hidden; }
.preview-box.dark { background: #1e293b; }
.preview-header { display: flex; gap: 6px; padding: 8px; }
.preview-dot { width: 10px; height: 10px; border-radius: 50%; background: var(--gray-light); }
.preview-content { padding: 8px; }
.preview-nav { height: 20px; background: var(--white); border-radius: 4px; margin-bottom: 8px; }
.preview-box.dark .preview-nav { background: #334155; }
.preview-card { height: 40px; background: var(--white); border-radius: 4px; margin-bottom: 8px; }
.preview-box.dark .preview-card { background: #334155; }
.preview-card.small { height: 25px; width: 60%; }
.data-info { margin-bottom: 1.5rem; }
.storage-usage { display: flex; align-items: center; gap: 0.5rem; color: var(--gray); font-size: 0.9rem; }
.data-options { display: flex; flex-direction: column; gap: 1rem; }
.data-card { display: flex; align-items: center; gap: 1rem; padding: 1.25rem; background: var(--light); border-radius: 12px; }
.data-card.danger { background: rgba(239, 68, 68, 0.05); }
.data-card-icon { width: 48px; height: 48px; border-radius: 12px; display: flex; align-items: center; justify-content: center; }
.data-card-icon.export { background: rgba(99, 102, 241, 0.1); color: var(--primary); }
.data-card-icon.import { background: rgba(16, 185, 129, 0.1); color: var(--success); }
.data-card-icon.delete { background: rgba(239, 68, 68, 0.1); color: var(--danger); }
.data-card-content { flex: 1; }
.data-card-content h4 { margin-bottom: 0.25rem; }
.data-card-content p { font-size: 0.85rem; color: var(--gray); }
.backup-reminder { display: flex; align-items: center; gap: 0.5rem; padding: 1rem; background: rgba(245, 158, 11, 0.1); color: var(--warning); border-radius: 10px; margin-top: 1rem; font-size: 0.9rem; }
.help-cards { display: grid; grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); gap: 1.5rem; }
.help-card { background: var(--light); border-radius: 12px; padding: 1.5rem; }
.help-card svg { color: var(--primary); margin-bottom: 0.75rem; }
.help-card h4 { margin-bottom: 0.5rem; }
.help-card p { font-size: 0.9rem; color: var(--gray); margin-bottom: 0.75rem; }
.help-card ul { list-style: none; font-size: 0.85rem; }
.help-card li { padding: 0.375rem 0; color: var(--gray); }
.keyboard-shortcuts { margin-top: 2rem; padding-top: 1.5rem; border-top: 1px solid var(--light); }
.keyboard-shortcuts h4 { margin-bottom: 1rem; }
.shortcuts-grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); gap: 1rem; }
.shortcut { display: flex; align-items: center; gap: 0.5rem; font-size: 0.9rem; }
.shortcut kbd { padding: 0.25rem 0.5rem; background: var(--light); border-radius: 4px; font-family: monospace; font-size: 0.85rem; }
.shortcut span { color: var(--gray); }
.export-options { display: flex; flex-direction: column; gap: 0.5rem; margin: 1rem 0; }
.export-option { display: flex; align-items: center; gap: 0.75rem; padding: 0.75rem; border: 2px solid var(--light); border-radius: 8px; cursor: pointer; }
.export-option.selected { border-color: var(--primary); background: rgba(99, 102, 241, 0.05); }
.export-option input { accent-color: var(--primary); }
.modal-actions { display: flex; justify-content: flex-end; gap: 0.75rem; margin-top: 1rem; }
.modal-content.danger { text-align: center; }
.danger-icon { color: var(--danger); margin-bottom: 1rem; }
.modal-content.danger ul { text-align: left; margin: 0.75rem 0; padding-left: 1.5rem; color: var(--gray); font-size: 0.9rem; }

/* App Layout with Footer */
.app-layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.app-layout .main-content {
  flex: 1;
}

/* Login Page Redesign */
.login-page-redesign {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  min-height: 100vh;
}

.login-showcase {
  background: linear-gradient(135deg, #6366f1 0%, #4f46e5 50%, #4338ca 100%);
  padding: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  overflow-y: auto;
}

.showcase-content {
  max-width: 700px;
  width: 100%;
}

.showcase-header {
  margin-bottom: 2.5rem;
}

.logo-large {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.logo-large svg {
  filter: drop-shadow(0 2px 4px rgba(0,0,0,0.2));
}

.showcase-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  line-height: 1.2;
}

.showcase-header p {
  font-size: 1.1rem;
  opacity: 0.9;
}

.showcase-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin-bottom: 2.5rem;
}

.stat-item {
  text-align: center;
  padding: 1rem;
  background: rgba(255,255,255,0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.stat-value {
  display: block;
  font-size: 1.75rem;
  font-weight: 700;
}

.stat-label {
  display: block;
  font-size: 0.8rem;
  opacity: 0.9;
  margin-top: 0.25rem;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 2.5rem;
}

.feature-item {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 1rem;
  background: rgba(255,255,255,0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  transition: all 0.2s;
}

.feature-item:hover {
  background: rgba(255,255,255,0.15);
  transform: translateY(-2px);
}

.feature-icon {
  width: 44px;
  height: 44px;
  border-radius: 10px;
  background: rgba(255,255,255,0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.feature-text h3 {
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.feature-text p {
  font-size: 0.8rem;
  opacity: 0.85;
  line-height: 1.4;
}

.testimonials-section {
  margin-bottom: 2rem;
}

.testimonials-section h2 {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.testimonial-card {
  background: rgba(255,255,255,0.1);
  padding: 1rem;
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.testimonial-card .stars {
  display: flex;
  gap: 2px;
  margin-bottom: 0.5rem;
}

.testimonial-card p {
  font-size: 0.85rem;
  line-height: 1.5;
  margin-bottom: 0.75rem;
  font-style: italic;
}

.testimonial-author strong {
  display: block;
  font-size: 0.85rem;
}

.testimonial-author span {
  font-size: 0.75rem;
  opacity: 0.8;
}

.trust-badges {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.trust-badges .badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  opacity: 0.9;
}

.login-form-section {
  background: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem;
  overflow-y: auto;
}

.login-form-container {
  max-width: 420px;
  width: 100%;
  margin: 0 auto;
}

.form-header {
  text-align: center;
  margin-bottom: 2rem;
}

.form-header h2 {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--dark);
  margin-bottom: 0.5rem;
}

.form-header p {
  color: var(--gray);
}

.tab-buttons {
  display: flex;
  background: var(--light);
  border-radius: 12px;
  padding: 4px;
  margin-bottom: 2rem;
}

.tab-btn {
  flex: 1;
  padding: 0.75rem;
  border: none;
  background: transparent;
  border-radius: 10px;
  font-weight: 500;
  color: var(--gray);
  cursor: pointer;
  transition: all 0.2s;
}

.tab-btn.active {
  background: var(--white);
  color: var(--primary);
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.form-field label {
  display: block;
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--dark);
  margin-bottom: 0.5rem;
}

.form-field .optional {
  color: var(--gray);
  font-weight: 400;
}

.input-wrapper {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.875rem 1rem;
  border: 2px solid var(--light);
  border-radius: 12px;
  background: var(--white);
  transition: all 0.2s;
}

.input-wrapper:focus-within {
  border-color: var(--primary);
  box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.1);
}

.input-wrapper svg {
  color: var(--gray);
  flex-shrink: 0;
}

.input-wrapper input {
  flex: 1;
  border: none;
  background: transparent;
  font-size: 1rem;
  color: var(--dark);
  outline: none;
}

.input-wrapper input::placeholder {
  color: #94a3b8;
}

.error-alert {
  padding: 0.875rem 1rem;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: 10px;
  color: var(--danger);
  font-size: 0.9rem;
}

.submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem 1.5rem;
  background: linear-gradient(135deg, #6366f1 0%, #4f46e5 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 0.5rem;
}

.submit-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(99, 102, 241, 0.35);
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.loading-dots {
  display: flex;
  gap: 4px;
}

.loading-dots span {
  width: 8px;
  height: 8px;
  background: white;
  border-radius: 50%;
  animation: loadingDot 1.2s ease-in-out infinite;
}

.loading-dots span:nth-child(2) { animation-delay: 0.2s; }
.loading-dots span:nth-child(3) { animation-delay: 0.4s; }

@keyframes loadingDot {
  0%, 100% { transform: scale(0.8); opacity: 0.5; }
  50% { transform: scale(1.2); opacity: 1; }
}

.form-footer {
  text-align: center;
  margin-top: 1.5rem;
  color: var(--gray);
}

.switch-btn {
  background: none;
  border: none;
  color: var(--primary);
  font-weight: 500;
  cursor: pointer;
}

.switch-btn:hover {
  text-decoration: underline;
}

.register-benefits {
  margin-top: 2rem;
  padding: 1.5rem;
  background: var(--light);
  border-radius: 16px;
}

.register-benefits h4 {
