body.dark-mode .login-form-section { background: #1e293b; }
body.dark-mode .form-header h2 { color: #e2e8f0; }
body.dark-mode .form-field label { color: #e2e8f0; }
body.dark-mode .input-wrapper { border-color: #334155; background: #1e293b; }
body.dark-mode .input-wrapper:focus-within { border-color: var(--primary); }
body.dark-mode .input-wrapper input { color: #e2e8f0; }
body.dark-mode .tab-buttons { background: #0f172a; }
body.dark-mode .tab-btn.active { background: #1e293b; color: var(--primary-light); }
body.dark-mode .register-benefits { background: #0f172a; }
body.dark-mode .register-benefits h4 { color: #e2e8f0; }
body.dark-mode .register-benefits li { color: #94a3b8; }
body.dark-mode .toast { background: #1e293b; box-shadow: 0 10px 30px rgba(0,0,0,0.4); }
body.dark-mode .toast-message { color: #e2e8f0; }
body.dark-mode .app-footer { background: #0f172a; }
body.dark-mode .app-footer p { color: #94a3b8; }
body.dark-mode .link-btn { color: var(--primary-light); }

/* Print Styles */
@media print {
  .main-nav, .ai-toggle-btn, .ai-panel, .nav-btn, .add-btn, .action-btn, .logout-btn, .btn-primary, .btn-secondary, .icon-btn, .close-btn, .modal-overlay { display: none !important; }
  body { background: white !important; color: black !important; }
  .main-content { max-width: 100%; padding: 0; }
  .dashboard-card, .stat-card, .invoice-card, .customer-card { box-shadow: none; border: 1px solid #ddd; page-break-inside: avoid; }
}

/* Responsive */
@media (max-width: 768px) {
  .nav-container { padding: 0 1rem; gap: 1rem; }
  .nav-right { gap: 0.5rem; }
  .nav-user span { display: none; }
  .nav-user { padding: 0.5rem; }
  .notification-dropdown { width: calc(100vw - 2rem); right: -50px; }
  .search-modal { margin: 0 1rem; }
  .features-preview { flex-direction: column; gap: 0.5rem; }
  .customers-section { grid-template-columns: 1fr; }
  .sidebar { order: -1; }
  .stats-bar, .stats-grid { grid-template-columns: 1fr 1fr; }
  .settings-container { grid-template-columns: 1fr; }
  .settings-sidebar { flex-direction: row; flex-wrap: wrap; }
  .swot-grid, .swot-form-grid { grid-template-columns: 1fr; }
  .quick-actions-grid { grid-template-columns: repeat(2, 1fr); }
  .dashboard-grid { grid-template-columns: 1fr; }
  .guides-grid { grid-template-columns: 1fr; }

  /* Login page responsive */
  .login-page-redesign { grid-template-columns: 1fr; }
  .login-showcase { display: none; }
  .login-form-section { padding: 2rem 1.5rem; }
  .login-form-container { max-width: 100%; }

  /* Footer responsive */
  .footer-content { grid-template-columns: 1fr; gap: 2rem; }
  .footer-links { gap: 2rem; flex-wrap: wrap; }
  .footer-bottom { flex-direction: column; text-align: center; }

  /* Toast responsive */
  .toast-container { left: 1rem; right: 1rem; max-width: none; }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .login-page-redesign { grid-template-columns: 1fr 1fr; }
  .showcase-stats { grid-template-columns: repeat(2, 1fr); }
  .features-grid { grid-template-columns: 1fr; }
  .testimonials-grid { grid-template-columns: 1fr; }
}
